
body{
  background-color: aliceblue;
}
.video-player {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
}

.percentage-bar {
  position: fixed;
  top: 0;
  right: 0;
  margin-right: 4rem;
  width: 40px;
  height: 40px;
  margin-top: .5rem;
}

.percentage-bar-outer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 5px solid rgb(224, 220, 220);
}

.percentage-bar-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  clip: rect(0px, 15px, 30px, 0px);
}


.video__title {
  font-size: 20px;
  margin-top: 3px;
  margin-right: 60rem;
}
.video__list {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  
}

.hr__bar {
  width: 55%;
  border: 1px solid gray;
  margin-top: 20px;
  margin-right: 35rem;
}

.video__desc {
  margin: 20px 0;
  margin-right: 20rem;
  font-size: 20px;
  width: 50%;

}
.course__videos{
  margin-right: 65rem;
  color: blue;
  font-size: 20px;
}

.videos {
  display: flex;
  justify-content: center;
}

.video__lists {
  width: 70%;
  margin-right: 50px;
  text-align: left;
  padding: 10px;
}

.course {
  font-size: 20px;
  margin-bottom: 20px;
}


.video__map {
  cursor: pointer;
}

.video__map:hover {
  background-color: lightgray;
}

.novideos {
  font-size: 18px;
  margin-top: 50px;
}

@media only screen and (max-width: 768px) {
  .video__lists {
    width: 100%;
    margin-right: 0;
  }

  .video-player {
    width: 100%;
  }
}
